import {customElement} from '@here/hds-base';
import {Accordion} from '@here/hds-accordion';
import {html, PropertyValues} from 'lit';
import {property} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {styles} from './hds-vertical-navigation.css.js';
import {VerticalNavigationItem} from './hds-vertical-navigation-item.js';

export type VerticalNavigationMode = 'collapsed' | 'expanded';
export type VerticalNavigationSelectPayload = {
  verticalNavItemId?: string;
  verticalSubListItemId?: string;
  verticalNavItemIndex?: number;
  verticalSubListItemIndex?: number;
};

/**
 * @slot default Content for `hds-vertical-navigation-item` elements structure
 *
 * @event {CustomEvent<{verticalNavItemId?: string; verticalSubListItemId?: string; verticalNavItemIndex?: number; verticalSubListItemIndex?: number;}>} select Fired when one of the navigation items has been selected `VerticalNavigationSelectPayload`
 */
@customElement('hds-vertical-navigation')
export class VerticalNavigation extends Accordion {
  @property({type: Boolean}) chevron = false;
  @property({type: String, reflect: true, attribute: 'nav-mode'}) navMode: VerticalNavigationMode =
    'expanded';

  static get styles(): any {
    return [super.styles, styles];
  }

  protected firstUpdated(_changedProperties: PropertyValues) {
    super.firstUpdated(_changedProperties);
    if (this.chevron) {
      this.arrowPosition = 'start';
    }
  }

  protected updated() {
    super.updated();
    this._updateNavItems();
  }

  protected render() {
    const classes = {
      'nav-expanded': this.navMode === 'expanded',
      'nav-collapsed': this.navMode === 'collapsed',
    };

    return html`
      <div
        class="-hds-vertical-navigation ${classMap(classes)}"
        @select="${this._handleSelected}"
        @subListItemSelect="${this._handleSelected}"
      >
        ${super.render()}
      </div>
    `;
  }

  private _updateNavItems() {
    const items = this._getNavItems();
    for (let i = 0; i < items.length; i += 1) {
      items[i].chevron = this.chevron;
      items[i].navMode = this.navMode;
    }
  }

  private _getNavItems() {
    return this.slotElement
      .assignedNodes({flatten: true})
      .filter((e: Node) => e instanceof VerticalNavigationItem) as VerticalNavigationItem[];
  }

  private async _handleSelected(evt: CustomEvent<VerticalNavigationSelectPayload>) {
    evt.stopPropagation();
    await this.updateComplete;

    this.dispatchEvent(
      new CustomEvent<VerticalNavigationSelectPayload>('select', {
        detail: evt.detail,
        bubbles: true,
        composed: true,
      })
    );
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-vertical-navigation': VerticalNavigation;
  }
}
